.taro_chooselocation {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
  top: 100%;
  background-color: #fff;
  -webkit-transition: ease top .3s;
  transition: ease top .3s;
  z-index: 1;
}

.taro_chooselocation_bar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 0 2.02667rem;
          flex: 0 2.02667rem;
  height: 2.02667rem;
  background-color: #ededed;
  color: #090909;
}

.taro_chooselocation_back {
  -webkit-flex: 0 0.96rem;
          flex: 0 0.96rem;
  position: relative;
  width: 0.704rem;
  height: 0.64rem;
  margin-top: 0.64rem;
}

.taro_chooselocation_back::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 0;
  height: 0;
  border: solid 0.32rem;
  border-top-color: transparent;
  border-right-color: #090909;
  border-bottom-color: transparent;
  border-left-color: transparent;
}

.taro_chooselocation_back::after {
  content: '';
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  top: 0;
  left: 0.064rem;
  border: solid 0.32rem;
  border-top-color: transparent;
  border-right-color: #ededed;
  border-bottom-color: transparent;
  border-left-color: transparent;
}

.taro_chooselocation_title {
  -webkit-flex: 1;
          flex: 1;
  line-height: 2.02667rem;
  padding-left: 0.64rem;
}

.taro_chooselocation_submit {
  width: 2.34667rem;
  height: 1.28rem;
  color: #fff;
  background-color: #08bf62;
  border: none;
  font-size: 0.59733rem;
  line-height: 1.28rem;
  padding: 0;
  margin: 0.384rem 0.64rem 0 0;
}

.taro_chooselocation_frame {
  -webkit-flex: 1;
          flex: 1;
}